}

.notificationContainer > img {
  width: 40px;
  height: 40px;
  padding: 0 10px 10px 10px;
}

.rankingViewContainer .circleNotification {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 1.2rem;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #2c8ffa;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.rankingViewContainer .notificationContainer:hover .circleNotification {
  background-color: #0d203b;
}

.rankingViewContainer .avatarBox {
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.rankingViewContainer .avatarBox > img {
  height: 60px;
  border: 2px solid #fff;
  border-radius: 50%;
}

.rankingViewContainer .avatarBox .welcomeMsg {
  color: #2c8ffa;
  font-weight: 700;
  margin-left: 10px;
}

.rankingViewContainer .logoutBtn {
  background-color: #fff;
  width: 40px;
  height: 40px;
  border: 2px solid #2c8ffa;
  border-radius: 4px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  cursor: pointer;
  outline: none;
}
.rankingViewContainer .logoutBtn:hover {
  border-color: #0d203b;
  background-color: #0d203b;
}

.logoutBtn > img {
  width: 60%;
}

.rankingViewContainer header {
  box-sizing: border-box;
  padding: 0 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  height: auto;
  width: 100%;
  background-color: #0d203b;
  overflow-y: scroll;
}

.rankingRecords {
  display: flex;
  flex-direction: column;
  width: 70vw;
  height: 90%;
  justify-content: flex-start;
  align-items: center;
}

.rankingRecord {
  display: flex;
  flex-direction: row;
  width: 70%;
  height: 80px;
  background-color: #2c8ffa;
  border: 2px solid #fff;
  border-radius: 4px;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 20px;
}

.rankingRecord .avatarBox .welcomeMsg {
  color: #fff;
  font-weight: 300;
}

.rankingRecord .avatarBox > img {
  height: 50px;
}

.placeCircle {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0d203b;
  color: #fff;
  border: 2px solid #fff;
  font-family: "Lato", sans-serif;
  font-size: 1.8rem;
  font-weight: 700;
}

.rankingScore {
  width: 60px;
  height: 40px;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0d203b;
  color: #fff;
  border: 2px solid #fff;
  font-size: 1.8rem;
  font-weight: 700;
  font-family: "Lato", sans-serif;
}

.faTrophyIcon {
  color: gold;
  font-size: 4rem;
}

