h1 {
  font-family: "Lato", sans-serif;
  font-size: 80px;
  color: #fff;
  font-weight: 300;
}

h2 {
  font-family: "Alegreya Sans SC", sans-serif;
  font-size: 50px;
  font-weight: 300;
  color: #2c8ffa;
  letter-spacing: 2px;
}

.leftContainer {
  display: flex;
  flex-direction: column;
  flex: 7;
  height: 90%;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.siteNameSection {
  display: flex;
  flex-direction: column;
  width: 90%;
  height: 50%;
  padding: 10px;
  justify-content: space-around;
  align-items: center;
  margin-top: 60px;
}

.mainLogo {
  width: 240px;
  height: 200px;
  margin-bottom: 40px;
}

.infoContainerBgc {
  position: absolute;
  left: -50px;
  bottom: -120px;
  z-index: 1;
}

.cardsAboutSiteSection {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 90%;
  height: 50%;
  padding: 10px;
}
